<template>
    <el-container id="bk" class="noisebk">
        <el-header>
            <p>myfile.link</p>
            <p>提供所见即所得的私人信息加密存储与回取服务</p>
        </el-header>
        <el-main>
            <el-col class="col" :xs="16" :sm="{ span: 16, offset: 2 }" :md="{ span: 10, offset: 3 }"
                :lg="{ span: 6, offset: 4 }" :xl="{ span: 6, offset: 4 }">
                <div class="registerLayer">
                    <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px"
                        class="demo-ruleForm">
                        <el-form-item label="用户名" prop="age">
                            <el-input v-model.number="ruleForm.name"></el-input>
                        </el-form-item>
                        <el-form-item label="密码" prop="pass">
                            <el-input type="password" v-model="ruleForm.pass" autocomplete="off"></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" style="margin:0 auto;" @click="onLogin">进入
                            </el-button>
                        </el-form-item>
                    </el-form>

                </div>
                <!-- <quill-editor id="editor" ref="editor" :options="editorOption" :disabled='step>1' v-model="content" @change="onEditorChange($event)"></quill-editor>
                <el-form>
                    <div class="selectitem">
                        <el-form-item label="请设置密码" style="font-weight: bold;">
                            <el-input v-model="password" type="password" placeholder=""></el-input>
                        </el-form-item>
                        <div class="selecttip"><i class="el-icon-warning" style="color:#F56C6C;"></i>myfile.link只会上传存储加密后信息</div>
                    </div>
                    <div class="selectitem">
                        <div class="toolbar">
                            <el-button type="primary" class="btn" :disabled="step!=1" @click="onSetPassword" size="small">上传</el-button>
                        </div>
                    </div>
                </el-form> -->
            </el-col>
        </el-main>
        <el-footer>
            <el-link type="primary" href="https://beian.miit.gov.cn/">蜀ICP备2021013463号-2</el-link>
        </el-footer>
    </el-container>

</template>

<style scoped>
#bk {
    height: 100vh;
    width: 100%;
    background-color: #304163;
    font-size: 18px;
    display: flex;
    flex-direction: column;
}

el-header,
el-footer {
    background-color: #516C8D;
    color: #fff;
    text-align: center;
    line-height: 60px;
}

el-main {
    height: 500px;
    width: 100%;
    flex: auto;
}

.header {
    background-image: url('../assets/bk2.jpg');
}

.registerLayer {
  height: 350px;
  padding: 0 10px;
  padding-right: 50px;
  padding-top: 35px;
  margin-top: 30px;
  /*border: 1px solid #bfedad;*/
}

.registerLayer > div {
  height: 50px;
  line-height: 50px;
}

.registerLayer > div > label {
  display: inline-block;
  width: 30%;
  line-height: 40px;
  color: dimgray;
  font-size: 14px;
}

.registerLayer > div > input {
  width: 70%;
  height: 40px;
  border-width: 0;
  border-bottom: 1px solid #5093d2;
}

.registerLayer > div > button {
  width: 100%;
  height: 40px;
  border-width: 0px;
  background-color: #5093d2;
  color: white;
  margin-top: 50px;
  border-radius: 3px;
  cursor: pointer;
}

</style>

<script>
export default {
    name: 'bk',
    components: {},
    data() {
        return {
            ruleForm:{}

        }
    },
    methods: {
        onLogin(){
          this.$msgbox({
            title: '错误',
            message:'请重新输入用户名或密码'
          })
        },
        writeMemo() {
            console.log('write memo')
            this.$router.push('WriteMemo')
            // window.location.href= '/writememo'
        },
        getMemo() {
            console.log('get memo')
            if (this.downloadcode === '') {
                this.bInput = false
                return
            }
            // var reg = new RegExp('^[0-9]*

            var url = this.downloadcode
            if (url.indexOf('http') === -1) {
                this.$router.push({
                    path: 'getmemo',
                    query: {
                        id: this.downloadcode
                    }
                })
            } else {
                this.$router.push(url)
            }
        }

    }
}
</script>



